import { Controller, Block } from '@ali/4ever-cangjie';
import { toggleFold } from '@ali/4ever-plugin-heading-group';

const setAncestorsUnfold = (controller: Controller, node: Block) => {
  // 先展开标题
  const groups = controller.query('getHeadingAncestors', node);
  const parents = groups.filter((group) => controller.userData.get(group, 'fold'));
  parents.forEach((parent) => {
    controller.run(
      'onAction',
      toggleFold({
        node: parent,
        fold: false,
      }),
    );
  });

  // 再展开议题
  // NOTE: 议题的层级比 H1 更高，如果大纲中存在 H1，则可能会被议题隐藏
  const subject = controller.query('getSubjectStartBlock', node);
  if (subject && subject !== node && controller.userData.get(subject, 'fold')) {
    controller.run(
      'onAction',
      toggleFold({
        node: subject,
        fold: false,
      }),
    );
  }
};

export default setAncestorsUnfold;
